@use "constant";
@import url("https://fonts.googleapis.com/css2?family=Nabla&display=swap");

nav {
  display: block;
  position: relative;
  width: 100%;
  padding-bottom: 20px;
  .row {
    align-items: center;
    width: 100%;
  }
  .logo-container {
    display: flex;
    align-items: center;
    width: 20%;
    margin-right: 50px;
    a {
      text-decoration: none;
    }
    img {
      width: 64px;
      height: 64px;
    }
    h1 {
      font-family: "Nabla", system-ui;
      font-optical-sizing: auto;
      font-weight: 400;
      font-style: normal;
      font-variation-settings: "EDPT" 100, "EHLT" 12;
      font-size: 2em;
      text-align: center;
      width: 100%;
    }
  }

  .search-container {
    display: flex;
    position: relative;
    width: 70%;
    align-items: center;
    input {
      // width: clamp(300px, 400px, 500px);
      width: 90%;
      padding: 10px 15px;
      border-radius: 10px;
      border: 1px solid gray;
    }
    .search-icon {
      position: absolute;
      right: 15%;
      opacity: 50%;
      color: black;
    }
  }
  .nav-menu {
    margin-top: 10px;
    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style-type: none;
      height: 50px;

      li {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        border-bottom: 1px solid constant.$color-blue;
        a {
          text-decoration: none;
          color: white;
          font-weight: bold;
        }
      }
      .active {
        border: 1px solid constant.$color-blue;
        border-bottom: transparent;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        a {
          color: constant.$color-red !important;
        }
      }
    }
  }
}
.version {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
  color: white;
  opacity: 0.5;
}
