@use "constant";

$width-new-movies-container: 835px;
$height-new-movies-container: 350px;
#home-page {
  .hot-movies-container {
    display: flex;
    // flex-direction: row;
    position: relative;
    width: $width-new-movies-container;
    height: $height-new-movies-container;
    margin-top: 25px;
    overflow: hidden;

    .arrow-icon {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 40%;
      opacity: 0.75;
      cursor: pointer;
      &:hover {
        opacity: 1;
      }
    }
    .arrow-right-icon {
      right: 0;
    }
    .arrow-left-icon {
      left: 0;
      z-index: 3;
    }
  }
  section {
    margin-top: 50px;
    padding-top: 50px;
    border-top: 1px solid constant.$color-gray;
    .nav-new-movies {
      display: flex;
      align-items: center;
      width: 100%;
      height: 50px;
      margin-bottom: 15px;
      h2 {
        margin-right: 50px;
        // height: 50px;
        text-align: center;
      }
      .nav-items {
        display: flex;
        align-items: center;
        .nav-item {
          padding: 10px 20px;
          cursor: pointer;
        }
        .active {
          color: constant.$color-red;
        }
      }
    }
    .list-movies-container {
      display: block;
      width: 100%;
      .movie-container {
        display: inline-block !important;
        margin-bottom: 20px;
      }
      .button-container {
        display: flex;
        width: 100%;
        height: 50px;
        position: relative;
        // align-items: flex-end;
        .icon {
          margin-left: 10px;
        }
      }
      .button-24 {
        font-family: "Times New Roman", Times, serif;
        right: 0;
        position: absolute;
        align-items: center;
        display: flex;
      }
    }
  }
  .movie-container {
    display: block;
    position: relative;
    width: 200px !important;
    height: 350px;
    margin-right: 10px;
    overflow: hidden;
    a {
      display: block;
      position: relative;
      width: 100%;
      height: 100%;
      text-decoration: none;
    }
    .movie-poster-container {
      display: flex;
      position: relative;
      width: 200px;
      height: 300px;
      overflow: hidden;
      cursor: pointer;
      background-color: constant.$color-gray;
      border-radius: 10px;
      img {
        width: 100%;
        height: 100%;
        transition: transform 0.3s ease-in-out;

        &:hover {
          transform: scale(1.2);
        }
      }
      p {
        position: absolute;
        background-color: constant.$color-red;
        color: white;
        padding: 5px 10px;
        border-top-right-radius: 10px;
        border-bottom-left-radius: 10px;
        border: 1px solid constant.$color-gray;
      }
      .status {
        left: 0;
        bottom: 0;
      }
      .year {
        right: 0;
      }
    }
    .movie-info-container {
      margin-top: 10px;
      h3 {
        color: constant.$color-orange;
        text-decoration: none;
        &:hover {
          color: constant.$color-red;
        }
      }
      p {
        opacity: 0.7;
        color: white;
        &:hover {
          color: constant.$color-red;
        }
      }
    }
  }
  .nav-category-container {
    display: block;
    margin-top: 40px;
    a {
      display: inline-block;
      padding: 10px 20px;
      background-color: constant.$color-gray;
      margin-right: 20px;
      margin-top: 20px;
      cursor: pointer;
      text-decoration: none;
      color: white;
      &:hover {
        background-color: constant.$color-green;
      }
    }
  }
}

#ads-hot-movies-container {
  margin-top: 25px;
  width: 245px;
  height: 350px;
  background-color: constant.$color-gray;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  overflow: hidden;
  img {
    object-fit: cover;
  }
}
