@use "constant";
@import url("https://fonts.googleapis.com/css2?family=Nabla&family=Tac+One&display=swap");

.movie-info-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 500px;
  width: 100%;
  height: 100%;
  //   outline: 1px solid red;
  .error {
    font-size: 2em;
    text-align: center;
  }
  .row {
    width: 100%;
  }
  .left-col {
    width: 70%;
    // outline: 1px solid green;
    .movie-info {
      background-color: constant.$color-dark-green;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
    }
    .poster {
      width: 50%;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .button-container {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 0;
        padding-top: 40px;
        padding-bottom: 20px;
        background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), black);
        button {
          align-items: center;
        }
        .icon {
          margin-right: 5px;
        }
      }
    }
    .info {
      width: 40%;
      margin-left: 5%;
      padding-top: 20px;

      h1 {
        text-transform: uppercase;
        color: constant.$color-orange;
        font-family: "Tac One", sans-serif;
      }
      h3 {
        opacity: 0.7;
      }
      .details {
        margin-top: 20px;
        p {
          margin-bottom: 10px;
          line-height: 1.6;
          span {
            opacity: 0.7;
            margin-right: 10px;
          }
        }
      }
    }
    .new-episodes-container {
      background-color: constant.$color-dark-green;
      margin-top: 20px;
      padding: 20px;
      border-radius: 5px;
      display: flex;
      width: 100%;
      align-items: center;
      h3 {
        width: fit-content;
        margin-right: 20px;
      }
      .episodes-container {
        display: flex;
        flex-wrap: wrap;
        p {
          padding: 5px 10px;
          background-color: constant.$color-gray;
          border-radius: 5px;
          cursor: pointer;
          margin-right: 10px;
          &:hover {
            background-color: constant.$color-green;
          }
        }
      }
    }
    .extra-tags {
      margin-top: 20px;
      opacity: 0.5;
    }
    .movie-description-container {
      margin-top: 20px;
      padding: 20px;
      background-color: constant.$color-dark-green;
      border-radius: 10px;
      h3 {
        margin-bottom: 10px;
      }
      p {
        line-height: 1.3;
        opacity: 0.8;
      }
    }
  }
  .right-col {
    width: 30%;
  }
}
